/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
}
fieldset,img {
   border: 0
}
a{
    text-decoration: none;
    color:#999999;
}
ul{
list-style: none;
}
.Content{
    background:#fff;
.projectList{
  background:#fff;
  .ant-breadcrumb{
    font-size:16px;
    font-family:'Microsoft YaHei';
    line-height:30px;
    font-weight:400;
    color:rgba(153,153,153,1);
    margin-bottom:40px;
  }
    .project-table{
        position: relative;
        .ant-table {
            line-height:0;
            background:#fff;
            margin-top:25px;
            .ant-select-selection{
                outline: none;
                color:#1890FF;
            }
        }   
        .ant-btn-primary:nth-child(2){
            position: absolute;
            bottom:16px;
            left:50px;
        }
        .ant-btn-primary:nth-child(3){
            position: absolute;
            bottom:16px;
            left:150px;
        }
        .ant-btn-primary:nth-child(4){
            position: absolute;
            bottom:16px;
            left:250px;
        }
       
        .yema{
            width:167px;
            height:14px;
            font-size:14px;
            font-family:"Microsoft YaHei";
            font-weight:400;
            color:rgba(128,128,128,1);
            position: absolute;
            bottom:40px;
            left:0;
            display:inline-block;
            i{
                    font-style: normal;
            }
        }
   }
}
}