/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
  }
  fieldset,img {
   border: 0
  }
  a{
    text-decoration: none;
    color:#999999;
  }
  ul{
  list-style: none;
  }
  .Content{
    background:#fff;
    .caipanlist{
        padding:0px 20px 0px 20px;
        position: relative;
        .ant-breadcrumb{
            font-size:16px;
            font-family:'Microsoft YaHei';
            line-height:30px;
            font-weight:400;
            color:rgba(153,153,153,1);
            margin-bottom:28px;
         }
         .num{
            i{
                font-style:normal;
                margin-left:5px;
            }
            margin-right:50px;
          }



        }
       

    }