/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
}
fieldset,img {
   border: 0
}
a{
    text-decoration: none;
    color:#999999;
}
ul{
list-style: none;
}
.Content{
    background:#fff;
    .chengjiaodetail{
        padding:0px 20px 0px 20px;
        position: relative;
        .ant-breadcrumb{
            font-size:16px;
            font-family:'Microsoft YaHei';
            line-height:30px;
            font-weight:400;
            color:rgba(153,153,153,1);
            margin-bottom:28px;
        }
    
        .base{
            width:100%;
            height:260px;
             .title{
                font-size:16px;
                font-family:'Microsoft YaHei';
                font-weight:400;
                color:rgba(51,51,51,1);
                line-height: 52px;
                border-bottom:1px solid rgba(237,237,237,1);
                height:72px;
             }
             .ulbase{
                 li{
                     float:left;
                    width:370px;
                    height:52px;
                    color:rgba(51,51,51,1);
                 }
             }
        }

        .family{
        dl{
            width:25%;
           float:left;
            dt{
                  a{
                      display:inline-block;
                      margin-bottom:47px;
                      margin-left:79px;
                  }
            }
            dd{
                img{
                    width:240px;
                    height:180px;
                    }
            }
        }
       }
   

    }
}