/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
  }
  fieldset,img {
   border: 0
  }
  a{
    text-decoration: none;
    color:#999999;
  }
  ul{
  list-style: none;
  }
  .Content{
    background:#fff;
    .total{
        padding:0px 20px 0px 20px;
        position: relative;
        .ant-breadcrumb{
            font-size:16px;
            font-family:'Microsoft YaHei';
            line-height:30px;
            font-weight:400;
            color:rgba(153,153,153,1);
            margin-bottom:28px;
         }
         .zongtable{
             .time{
                 li{
                    float:left;
                    margin-right:32px;
                 }
                .current{
                  color:rgba(45,164,255,1);
                }
             }
             table{
                border:1px solid rgba(230,230,230,1);
                width:1580px;
                margin-top:30px;
                // .kuan{
                //     width:140px;
                //     height:50px;
                // }
                  thead{
                     th{
                         width:70px;
                         height:50px;
                        text-align: center;
                     }
                  }
                  tbody{
                    td{
                        width:70px;
                        height:50px;
                        text-align: center;
                    }
                  }
             }
         }
   




        }
    }