/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
}
fieldset,img {
   border: 0
}
a{
    text-decoration: none;
    color:#999999;
}
ul{
list-style: none;
}
 
.Content{
    background:#fff;
    .photomanage{
        .ant-upload-list-picture-card {
            .ant-upload-list-item-thumbnail{
                img {
                    width:274px;
                    height: 202px;
                }
            }
        }
        padding:0px 20px 0px 20px;
        position: relative;
        .top{
            height:60px;
        .ant-breadcrumb{
            font-size:16px;
            font-family:'Microsoft YaHei';
            line-height:30px;
            font-weight:400;
            color:rgba(153,153,153,1);
            margin-bottom:28px;
        }
        .box{
            position: relative;
             .ant-btn-primary{
                 position: absolute;
                 left:1000px;
                 top:10px;
             }
            width:64px;
            height:32px;
            
        }
        }
        .myupload{
          
            .file {
                position: relative;
                display: inline-block;
                background: #D0EEFF;
                border: 1px solid #99D3F5;
                border-radius: 4px;
                padding: 4px 12px;
                overflow: hidden;
                color: #1E88C7;
                text-decoration: none;
                text-indent: 0;
                line-height: 20px;
                input {
                    position: absolute;
                    font-size: 100px;
                    right: 0;
                    top: 0;
                    opacity: 0;
                }
            }
          
            .file:hover {
                background: #AADFFD;
                border-color: #78C3F3;
                color: #004974;
                text-decoration: none;
            }
        #preview{
            .piclist{
                float:left;
                img{
                      width:292px;
                      height:220px;
                      margin-right:30px;
                      margin-bottom:20px;
                }
                input{
                    width:251px;
                    height:28px;
                    margin-bottom:20px;
                    border-radius:5px;
                    border:1px solid rgba(230,230,230,1);
                    
                }
            }
        }
            
             
           
        }

         .ant-form-item-label{
            //  margin-top:30px;
            //  margin-bottom:20px;
            //  font-size:16px;
         }
         .ant-tabs-tab{
             font-size:18px;
         }
      .ant-tabs-tabpane-active{
          position:relative;
         .ant-form-item-control {
            //   min-height:150px;
           
            .ant-upload-list-item-done{
                width:292px;
                height:220px;
                margin-right:30px;
                margin-bottom:40px;
            }
            .ant-upload{
                width:160px;
                height:160px;
             }
         }
        }
    }
}