/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
  }
  fieldset,img {
   border: 0
  }
  a{
    text-decoration: none;
    color:#999999;
  }
  ul{
  list-style: none;
  }
  .Content{
    background:#fff;
    .cusfollowcycle{
        padding:0px 20px 0px 20px;
        position: relative;
        .ant-breadcrumb{
            font-size:16px;
            font-family:'Microsoft YaHei';
            line-height:30px;
            font-weight:400;
            color:rgba(153,153,153,1);
            margin-bottom:28px;
         }
        .option{
            margin-top:20px;
            position: relative;
            
            .time{
                .current{
                    color:rgba(24,144,255,1)
                }
                 li{
                       cursor:pointer;
                       float:left;
                       margin-right:40px;
                       font-size:16px;
                 }  
            }
           .title{
            font-size:16px;
            font-family:'MicrosoftYaHei';
            font-weight:400;
            color:rgba(128,128,128,1);
           }
            .detail1{
                color:rgba(24,144,255,1);
                position: absolute;
                top:85px;
                left:1100px;
            }
            .detail2{
                color:rgba(24,144,255,1);
                 position: absolute;
                 top:106px;
                 left:1100px;
            }
        }
   
            .detail{
                // width:100px;
                float:left;
                 margin-top:103px;

            }



        }
    }