/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
}
fieldset,img {
   border: 0
}
a{
    text-decoration: none;
    color:#999999;
}
ul{
list-style: none;
}

.index{
    .ant-layout-header{
        border-bottom:1px solid rgba(230,230,230,1);
    }
    .logo{
        h1{
            font-size:20px;
            font-family:"Microsoft YaHei";
            font-weight:400;
            color:rgba(255,255,255,1);
            padding-top:1.5rem;
            
        }
    }
  
 
   .ant-layout{
        .ant-layout-header{
            height:7.4vh;
           .userbox{
               float:right;
               .name{
                   margin-right:50px;
                   margin-left:20px;
                   font-size:18px;
                    font-family:'Microsoft YaHei';
                    font-weight:400;
                    color:rgba(51,51,51,1);
               }
           }
           
         }
         .ant-layout-content{
            .my-content{
                     width:84.3vw;
                    height:51.8vh;
                .leftPie{
                    width:27.3vw;
                    height:51.8vh;
                    // background:#0ff;
                    float:left;
                    .title{
                        font-size:20px;
                        line-height:30px;
                        font-family:'Microsoft YaHei';
                        font-weight:400;
                        color:rgba(50,50,50,1);
                    }
                }
                .midMap{
                    width:26vw;
                    height:51.8vh;
                    background:#fff;
                      float:left;
                }
                .rigPie{
                    width:30.7vw;
                    height:51.8vh;
                    // background:rgb(230, 168, 211);
                    float:left;
                    .title{
                        font-size:20px;
                        line-height:30px;
                        font-family:'Microsoft YaHei';
                        font-weight:400;
                        color:rgba(50,50,50,1);
                    }
                }
               
            }
            .import-cus{
                width:84.3vw;
                height:51.8vh;
               background:#fff;
               .customer{
                width:57.2vw;
                height:51.8vh;
                // background:rgb(86, 183, 196);
                float:left;
                position:relative;
                .title{
                    font-size:20px;
                    font-family:'Microsoft YaHei';
                    font-weight:400;
                    color:rgba(50,50,50,1);
                } 
                 .ant-table {
                    line-height: 0.1;
                    background:#fff;
                    margin-top:20px;
                }          
                p{
                    position: absolute;
                   bottom:10px;
                }
              
                }
                .notice{
                    width:27vw;
                    height:51.8vh;
                    // background:rgb(143, 163, 127);
                    float:left;
                    .title{
                        font-size:20px;
                        font-family:'Microsoft YaHei';
                        font-weight:400;
                        color:rgba(50,50,50,1);
                    }
                    .public-notice{
                        width:26vw;
                        height:30.5vh;
                        // background:#ff0;
                        border-radius:12px;
                        border:1px solid #E6E6E6;
                        margin-top:25px;
                        ul{
                            margin-left:40px;
                            margin-top:20px;
                            li{
                                line-height:32px;
                                font-size:16px;
                                font-family:'Microsoft YaHei';
                                font-weight:400;
                                color:rgba(102,102,102,1);
                                span{
                                    margin-right:40px;
                                }
                                i{
                                    font-style:normal;
                                }
                            }
                        }
                    }
                }
                
         }  
            
          
          
         }


   }








}