/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
}
fieldset,img {
   border: 0
}
a{
    text-decoration: none;
    color:#999999;
}
ul{
list-style: none;
}
input::-webkit-input-placeholder {
    /* WebKit browsers */
    color:rgba(179,179,179,1);
  }
  input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color:rgba(179,179,179,1);
  }
  input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: rgba(179,179,179,1);
  }
  input::-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color:rgba(179,179,179,1);
  }

    .login{
      width:100%;
      height:100%;
      position: relative;
        .loginbox{
            h1{
                font-size:24px;
                font-family:'SourceHanSansSC-Regular';
                font-weight:400;
                color:rgba(31,126,254,1);
                text-align: center;
                line-height: 84px;
            }
            width:500px;
            height:400px;
            padding:0 70px 0 70px;
            box-shadow:0px 0px 15px rgba(0, 0, 0, 0.06);
            border-radius:6px;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -200px;
            margin-left: -250px;
            .box{
                margin-bottom: 3px;
                height:59px;
                input{
                    width:330px;
                     height:58px;
                     margin-left:10px;
                      border:none;
                     outline: none;
                     font-size:16px;
                     color:rgb(15, 15, 15)
               }
               .yan{
                width:250px;
               }
            }
             
        }
       
    }
