/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
  }
  fieldset,img {
   border: 0
  }
  a{
    text-decoration: none;
    color:#999999;
  }
  ul{
  list-style: none;
  }
  .Content{
    background:#fff;
    .kedetail{
        padding:0px 20px 0px 20px;
        position: relative;
        .ant-breadcrumb{
            font-size:16px;
            font-family:'Microsoft YaHei';
            line-height:30px;
            font-weight:400;
            color:rgba(153,153,153,1);
            margin-bottom:28px;
         }
        .option{
            margin-bottom:50px;
            margin-top:20px;
            position: relative;
            .time{
                 float:right;
                li{
                    float:left;
                    margin-right:32px;
                    font-size:16px;
                    font-family:'MicrosoftYaHei';
                    font-weight:400;
                }
                .current{
                        color:rgba(24,144,255,1)
                }
               
            }
        }
       table{
             width:100%;
             border:1px solid rgba(230,230,230,1);
             margin-top:98px;
             tr{
                 height:50px;
                 th{
                     width:16.6%;
                    text-align: center;
                 }
                 td{
                       text-align: center;
                 }
             }
       }




        }
    }