/*重置样式*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
    margin: 0;
    padding: 0;
   color: #999999;
   font-family:"Microsoft YaHei";
}
fieldset,img {
   border: 0
}
a{
    text-decoration: none;
    color:#999999;
}
ul{
list-style: none;
}
